.header {
  padding: 20px 165px;
  display: flex;
  align-items: center;
  position: relative;
  justify-content: space-between;
}

.header__logo-container {
  margin-left: 10px;
  display: flex;
  gap: 5.44px;
  align-items: center;
}

.header__button-container {
  display: flex;
  gap: 32px;
}

.header__nav {
  display: flex;
  gap: 12px;
}

.header__nav-link {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  display: flex;
  align-items: center;
  color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
  transition: opasity 0.3s ease-in-out;
}

.header__nav-link:hover {
  opacity: 0.6;
}

.header__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 173px;
  height: 36px;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.85);
  box-shadow: 0px 1px 4px 1px rgba(36, 36, 36, 0.07);
  border-radius: 120px;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  display: flex;
  align-items: center;
  cursor: pointer;
  border: none;
  transition: opasity 0.3s ease-in-out;
}

.header__button:hover {
  opacity: 0.6;
}
